import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import normalize from 'normalize.css'
import $ from "jquery";
import boostrap from 'bootstrap'
import '../css/main.styl'

Vue.use(VueRouter)

const Products = resolve => require(['../components/product.vue'], resolve)
const Main = resolve => require(['../components/main.vue'], resolve)
const About = resolve => require(['../components/about.vue'], resolve)
const AboutNZ = resolve => require(['../components/about-nz.vue'], resolve)

const routes = [
	{ path: '/', component: Main },
  { path: '/products', component: Products },
  { path: '/about', component: About },
  { path: '/about-nz', component: AboutNZ }
]

const router = new VueRouter({
  routes 
})

const app = new Vue({
	el:'#app',
  router,
  render: h => h(App)
})



